.program {
  padding-top: 38px;
  padding-bottom: 38px;
}

.title--primary,
.card__title {
  font-family: var(--title-font);
  font-size: 24px;
  line-height: 2.71;
  letter-spacing: 0.01em;
  text-align: center;

  color: var(--primary-text-color);
}

.title--primary {
  @include mq(tablet) {
    text-align: left;
  }
}

@include mq(desktop) {
  .card__title.card__title--font {
    font-size: 24px;
    margin-bottom: 16px;
  }

  .title--primary {
    font-size: 36px;
  }

  .list__text.list__text--font,
  .list__item.list__item--font {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .card__text.card__text--font {
    font-size: 16px;
    margin-bottom: 15px;
  }
}

.list__item {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: '> ';
  }
}

.list__item,
.list__text,
.card__text {
  font-family: var(--primary-font);
  font-size: 16px;
  line-height: 1.25;

  color: var(--primary-text-color);
}

.list__text {
  &--upper {
    margin-bottom: 10px;
    margin-top: 0;
  }

  &--lower {
    margin-top: 10px;
    margin-bottom: 0;
    transition: transform var(--animation), visibility var(--animation), opacity var(--animation);
  }
}

.is-hidden {
  @include mq(notDesktop) {
    position: fixed;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;

    transform: translateY(1em);
    transition: transform var(--animation), visibility var(--animation), opacity evar(--animation);
  }
}



.button--read-more {
  
  font-family: var(--title-font);
  text-align: left;
  font-size: 18px;
  height: 65px;
  border: none;
  color: var(--hover-color);
  background-color: transparent;
  transform: translateY(0em);
  transition: transform var(--animation);

  &:hover,
  &:focus {
    color: darken(#876655, 15%);
  }

  @include mq(desktop) {
    display: none;
  }
}

.button__is-hidden {
  position: fixed;
  transform: translateY(1em);
  visibility: hidden;
  transition: transform var(--animation);
}

.box {
  @include mq(tablet) {
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }
}

.card--box {
  display: flex;
  flex-wrap: wrap;

  @include mq(tablet-only) {
    width: 298px;
  }

  @include mq(desktop) {
    justify-content: space-around;
    width: 392px;
  }
}

.text--box {
  @include mq(tablet) {
    width: 298px;
    margin-right: 40px;
    text-align: left;
  }

  @include mq(desktop) {
    width: 392px;
  }
}

.card {
  position: relative;
  width: 142px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &--1 {
    margin-right: 6px;
    margin-bottom: 25px;
    height: 176px;

    @include mq(desktop) {
      width: 180px;
      height: 223px;
    }
  }

  &--2 {
    margin-top: 33px;
    height: 168px;

    @include mq(desktop) {
      width: 180px;
      height: 215px;
    }
  }

  &--3 {
    margin-right: 5px;
    height: 173px;

    @include mq(desktop) {
      width: 180px;
      height: 220px;
    }
  }

  &--4 {
    margin-top: 33px;
    height: 170px;

    @include mq(desktop) {
      width: 180px;
      height: 215px;
    }
  }

  &__title {
    font-family: var(--title-font);
    font-size: 18px;
    line-height: 1.25;
    margin-bottom: 13px;
  }

  &__text {
    font-size: 14px;
    line-height: 1.21;
    margin-bottom: 13px;
    text-align: inherit;
  }

  &__svg {
    margin-bottom: 9px;
  }
}

.card__img {
  margin-bottom: 4px;

  &--1 {
    width: 77px;
    height: 81px;

    @include mq(desktop) {
      width: 97px;
      height: 107px;
    }
  }

  &--2 {
    width: 65px;
    height: 72px;

    @include mq(desktop) {
      width: 83px;
      height: 92px;
    }
  }

  &--3 {
    width: 70px;
    height: 77px;

    @include mq(desktop) {
      width: 88px;
      height: 97px;
    }
  }

  &--4 {
    width: 65px;
    height: 73px;

    @include mq(desktop) {
      width: 83px;
      height: 92px;
      margin-bottom: 6px;
    }
  }
}

.card__circle {
  position: absolute;
  bottom: 0;
  width: 142px;
  height: 142px;
  background-color: var(--circle-color);
  box-shadow: var(--shadow-circle);
  border-radius: 50%;
  z-index: -1;

  @include mq(desktop) {
    width: 180px;
    height: 180px;
  }
}
