.logo {
  margin-top: 7px;
  margin-right: auto;
  margin-left: auto;

  @include mq(tablet) {
    margin-top: 7px;
    margin-right: auto;
    margin-left: 0;
  }

  @include mq(desktop) {
    margin-top: 31px;
    margin-right: 38px;
  }
}
.social-list {
  display: inline-flex;
  margin-top: 30px;
  @include mq(tablet) {
    margin-top: 18px;
  }

  @include mq(desktop) {
    margin-top: 35px;
  }

  &__link {
    display: flex;

    align-items: center;

    @include mq(notDesktop) {
      align-items: center;
      justify-content: center;
      align-items: center;
      width: 26px;
      height: 25px;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.1);
      transition: background-color var(--animation);
      &:hover,
      &:focus {
        fill: var(--modal-primary-color);
      }
    }
  }

  &__icon {
    width: 19px;
    height: 14px;
    fill: var(--icons-color);
    transition: fill var(--animation);
    .social-list__link:hover &,
    .social-list__link:focus & {
      fill: var(--hover-color);
    }
  }
}
.social-list__item {
  margin-right: 12px;
}
.navigation {
  display: flex;
  align-items: center;
  margin-right: auto;
  @include mq(notDesktop) {
    align-items: center;
  }
}

.navigation-list {
  display: inline-flex;
  font-size: 18px;
  margin-right: auto;

  @include mq(notDesktop) {
    display: none;
  }

  &__item:not(:last-child) {
    margin-right: 20px;
  }

  &__item:hover,
  &__item:focus,
  &__item:active {
    color: var(--hover-color);
  }
}

.navigation__link {
  color: inherit;
}

.hero {
  &__container {
    display: flex;
    align-items: center;
    flex-direction: column;

    @include mq(tablet) {
      align-items: flex-start;
      padding-bottom: 80px;
    }

    @include mq(desktop) {
      padding-bottom: 56px;
    }
  }
  &__tittle {
    color: var(--primary-text-color);
    font-family: var(--title-font);
    font-size: 30px;
    line-height: 38px;
    align-items: center;
    vertical-align: top;
    margin-top: 33px;

    @include mq(tablet) {
      margin-top: 10px;
    }
    @include mq(desktop) {
      font-size: 64px;
      line-height: 65px;
      margin-top: 57px;
    }
  }
  &__label {
    margin-top: 3px;
    margin-bottom: 20px;
    text-align: center;

    @media screen and (min-width: 1280px) {
      font-size: 24px;
      line-height: 38px;
      margin-top: 15px;
      margin-bottom: 10px;
    }
  }

  &__button {
    margin-right: auto;
    margin-left: auto;

    @include mq(tablet) {
      margin-left: auto;
    }
    @include mq(desktop) {
      margin-left: auto;

      @include mq(tablet) {
        margin-bottom: 15px;
      }
      @include mq(desktop) {
        margin-bottom: 25px;
      }
    }
  }
}

.reg {
  color: var(--secondary-bgr-color);
}
